.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper {
  display: flex;
  flex-direction: column;
  border: 1px solid #f1e3e3;
  background-color: rgba(255, 252, 252, 0.863);
  gap: 16px;
  padding: 16px;
  border-radius: 6px;
  box-shadow: 0px 4px 15px 0px #68676726;
}
.controls {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}
